@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@600&display=swap);


.yoruq{
    position: absolute;
    font-size: 100px;
    cursor: pointer;
    margin: 0;
}

.yoruq.hide {
    display: none;
}

.qorongu{  
    font-size: 100px;
    display: none;
    cursor: pointer;
    margin: 0;
}

.qorongu.show{
    display: block;
}

.qora{
    background-color: #000;
}
.tyomniy{
    display: flex;
    justify-content: flex-end;
    /* margin-top: -60px; */
    height: 100vh;
}
body{
    background-color: #fff;
    margin: 0;
    padding: 0;
}
.select{
    top: 30px;
    position: absolute;
    left: 30px;
    width: 250px;
    height: 80px;
    font-family: 'Cinzel', serif;
    font-size: 44px;
    text-align: center;
    border-radius: 5px;
}
.select:focus{
    outline: none;
}
.option:focus{
    outline: none;
}
.text{
    text-align: center; 
    margin-top: 50px;
    font-family: 'Cinzel', serif;
    color: #ccc;
}
.text__div{
    top: 0;
    position: absolute;
    width: 450px;
    font-size: 17px;
}
.text__container{
    display: flex;
    justify-content: center;
}
.form__container{
    display: flex;
    justify-content: center;
}
.form__div{
    position: absolute;
    top: 230px;
}
.inputname{
    display: block;
    margin-bottom: 15px;
    width: 200px;
    padding-left: 10px;
    font-family: 'Cinzel', serif;
    padding-bottom: 10px;
    padding-top: 10px;
    font-size: 16px;
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 1px;
    margin-top: 36px;
}
.inputpassword{
    display: block;
    width: 200px;
    padding-left: 10px;
    padding-bottom: 10px;
    padding-top: 10px;
    font-family: 'Cinzel', serif;
    font-size: 16px;
    margin-bottom: 30px;
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 1px;
}
.inputpassword:focus{
    outline: none;
}
.inputpassword:-ms-input-placeholder{
    color: #fff;
}
.inputpassword::placeholder{
    color: #fff;
}
.inputname:-ms-input-placeholder{
    color: #fff;
}
.inputname::placeholder{
    color: #fff;
}
.inputname:focus{
    outline: none;
}
.form{
    background-color: #ccc;
    padding-bottom: 30px;
    padding-top: 80px;
    padding-left: 100px;
    padding-right: 100px;
}
.form__title{
    position: absolute;
    margin-top: -66px;
    font-size: 36px;
    font-family: 'Cinzel', serif;
    margin-left: 53px;
}
.nametogri{
    position: absolute;
    margin-left: 180px;
    margin-top: 38px;
    display: none;
}
.namenotogri{
    position: absolute;
    margin-left: 180px;
    margin-top: 38px;
}
.passwordtogri{
    position: absolute;
    margin-left: 180px;
    margin-top: 3px;
    display: none;
}
.passwordnotogri{
    position: absolute;
    margin-left: 180px;
    margin-top: 3px;
}
.form__btn{
    cursor: pointer;
    width: 160px;
    height: 41px;
    margin-left: 26px;
    border: 2px solid black;
    border-radius: 2px;
    background-color: black;
    color: #fff;
    font-family: 'Cinzel', serif;
    font-size: 18px;
}
.form__btn:hover{
    background-color: transparent;
    color: black;
    transition: 1s ease all;
}
.form__text{
    position: absolute;
    margin-top: -20px;
    font-size: 18px;
    margin-left: 21px;
    font-family: 'Cinzel', serif;
    display: none;
    text-align: center;
}

